    export default {
        components: {
			videoItem
        },
        data() {
            return {
                tabIndex: 0,
                tabBars: [{
                    name: '精彩片段',
                    id: 'duanpian'
                }, {
                    name: '文字剧情',
                    id: 'pinglun'
                }],
				sortTabIndex: 0,
				sortTabBars: [{
					name: '按集数',
					id: 'jishu'
				}, {
					name: '按人气',
					id: 'renqi'
				}, {
					name: '按播放量',
					id: 'bofangliang'
				}],
				sortJishuIndex: 0,
				sortJishuBars: [1,2,3,4,5,6,7,8,9],
				sortJuqingIndex: 0,
				sortJuqingBars: [1,2,3,4,5,6,7,8,9],
            }
        },
        onLoad: function() {
            // this.newsitems = this.randomfn()
        },
        methods: {
			goBack() {
				uni.navigateBack({
					delta: 1
				});
			},
            tapTab(e) { //点击tab-bar
                if (this.tabIndex === e.target.dataset.current) {
                    return false;
                } else {
                    this.tabIndex = e.target.dataset.current
                }
            },
			sortTapTab(e) {
				if (this.sortTabIndex === e.target.dataset.current) {
					return false;
				} else {
					this.sortTabIndex = e.target.dataset.current
				}
			},
			sortJishu(e){
				if (this.sortJishuIndex === e.target.dataset.current) {
					return false;
				} else {
					this.sortJishuIndex = e.target.dataset.current
				}
			},
			sortJuqing(e) {
				if (this.sortJuqingIndex === e.target.dataset.current) {
					return false;
				} else {
					this.sortJuqingIndex = e.target.dataset.current
				}
			}
        }
    }



